import styled, { injectGlobal } from 'styled-components'
import { colors } from '../styles/variables'

import GraphikRegularEot from '../assets/fonts/Graphik-Regular-Web.eot'
import GraphikRegularWoff from '../assets/fonts/Graphik-Regular-Web.woff'
import GraphikRegularWoff2 from '../assets/fonts/Graphik-Regular-Web.woff2'

import bicefal2 from '../assets/images/bicefal2-hayk-an-design.png'

console.log(GraphikRegularEot)
console.log(GraphikRegularWoff)
console.log(GraphikRegularWoff2)
console.log(bicefal2)

injectGlobal`
	body {
		margin: 0;
	  padding: 0;
		font-family: 'GraphikWeb', serif;
  }

  ::-moz-selection {
  	color: ${colors.red};
  	background-color: ${colors.red};
  }
	::selection {
		color: ${colors.red};
		background-color: ${colors.red};
	}

	@font-face {
	  font-family: 'GraphikWeb';
	  src:
			url(${GraphikRegularEot}?#iefix) format("embedded-opentype"),
			url(${GraphikRegularWoff}) format("woff"),
			url(${GraphikRegularWoff2}) format("woff2"),
	  font-weight: 400;
	  font-style: normal;
	}
`
const BaseStyles = styled.div`

	color: #666;
	font-size: 18px;
	line-height: 24px;

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	*,
	*:before,
	*:after {
		box-sizing: border-box;
	}

	img {
		max-width: 100%;
	}
`

export default BaseStyles



